import React from "react";
import { useNavigate } from "react-router-dom";

const LogoutButton = () => {
  const navigate = useNavigate();

  const handleLogout = async () => {
    try {
      // Realiza una solicitud al servidor para cerrar sesin
      const response = await fetch("http://localhost:8000/auth/logout", {
        method: "GET",
        credentials: "include", // Incluye las cookies en la solicitud
      });

      if (response.ok) {
        // Si la solicitud es exitosa, redirige a la pgina de inicio de sesin u otra pgina
        navigate("/"); // Redirige a la pgina de inicio de sesin
      } else {
        // Si la solicitud no es exitosa, muestra un mensaje de error
        console.error("Error al cerrar sesin:", response.statusText);
      }
    } catch (error) {
      console.error("Error al cerrar sesin:", error.message);
    }
  };

  return <button onClick={handleLogout}>Cerrar Sesin</button>;
};

export default LogoutButton;
